}

.ant-carousel .slick-slide {
    text-align: center;
    height: 160px;
    line-height: 160px;
    background: #364d79;
    overflow: hidden;
}

    .ant-carousel .slick-slide h3 {
        color: #fff;
    }


.description {
    font-family: 'Montserrat', sans-serif;
